<template>
  <div class="p2">
    <!--增值税补充申报表-->
    <p style="font-size:20px;height:35px; font-weight:bold;text-align: center;">增值税补充申报表</p>
    <P style="width:50%; text-align:left; float:left;">纳税人识别号：{{com.firm_code}}</P>
    <P style="width:50%; text-align:left; float:left;">纳税人名称（公章）：{{com.firm_name}}</P>
    <p style="text-align:left; height:65px;">经营地址：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{com.firm_operating}}</p>
    <p style="width:33%; float:left; text-align:left;">法定代表人：&nbsp;&nbsp;&nbsp;{{com.firm_legal_person}}</p>
    <p style="width:34%; float:left; text-align:left;">财务负责人：{{com.telephone_name}}</p>
    <p style="width:33%; float:left; text-align:left;">联系电话：{{com.firm_telephone}}</p>
    <p style="width:50%; float:left; text-align:left;">办税人员：{{com.taxation_name}}</p>
    <p style="width:50%; float:left; text-align:left;">所属期：{{com.month}}-01日至{{com.month}}-30日</p>
    <p style="width:50%; float:left; text-align:left;">登记行业：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{com.firm_registration_trade}}</p>
    <p style="width:50%; float:left; text-align:left;">明细行业：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{com.firm_detail_trade}}</p>
    <form>
      <table cellpadding="0" cellspacing="0" border="1" width="100%">
        <tr>
          <td width="10%">编&nbsp;&nbsp;&nbsp;&nbsp;号</td>
          <td width="40%">数据项</td>
          <td width="10%">本期</td>
          <td width="40%">填报说明</td>
        </tr>
        <tr>
          <td>Q0002</td>
          <td>银行结算及刷卡收入（元）</td>
          <td style="background-color:#d6ebff;"><input name="1_1" tabindex="1" value="0.00"/></td>
          <td>通过银行结算或刷卡取得的货款或应税劳务含税收入合计</td>
        </tr>
        <tr>
          <td>Q0021</td>
          <td>现金收款收入（元）</td>
          <td style="background-color:#d6ebff;"><input name="2_1" tabindex="2" value="0.00"/></td>
          <td>以现金形式收取的货款或应税劳务含税收入合计</td>
        </tr>
        <tr>
          <td>Q0022</td>
          <td>未收款收入（元）</td>
          <td style="background-color:#d6ebff;"><input name="3_1" tabindex="3" value="0.00"/></td>
          <td>全部货款和应税劳务收入中除上两项之外的含税收入</td>
        </tr>
        <tr>
          <td>Q0003</td>
          <td>现金支出（元）</td>
          <td style="background-color:#d6ebff;"><input name="4_1" tabindex="4" value="0.00"/></td>
          <td>现金日记账贷方发生额合计</td>
        </tr>
        <tr>
          <td>Q0004</td>
          <td>应收账款借方余额（元）</td>
          <td style="background-color:#d6ebff;"><input name="5_1" tabindex="5" value="0.00"/></td>
          <td>应收账款科目期末借方余额</td>
        </tr>
        <tr>
          <td>Q0038</td>
          <td>制造费用借方发生额（元）</td>
          <td style="background-color:#d6ebff;"><input name="6_1" tabindex="6" value="0.00"/></td>
          <td>制造费用科目借方发生额合计</td>
        </tr>
        <tr>
          <td>Q0606</td>
          <td>购进货物用于集体福利及个人消费转出进项税额（元）</td>
          <td style="background-color:#d6ebff;"><input name="7_1" tabindex="7" value="0.00"/></td>
          <td>本期进项转出税额中属于购进货物用于集体福利及个人消费的部分</td>
        </tr>
        <tr>
          <td>Q0607</td>
          <td>应付福利费借方发生额（元）</td>
          <td style="background-color:#d6ebff;"><input name="8_1" tabindex="8" value="0.00"/></td>
          <td>本期应付福利费科目借方发生额合计</td>
        </tr>
        <tr>
          <td>Q0608</td>
          <td>在建工程项目借方发生额（元）</td>
          <td style="background-color:#d6ebff;"><input name="9_1" tabindex="9" value="0.00"/></td>
          <td>本期在建工程科目借方发生额合计</td>
        </tr>
        <tr>
          <td>Q0054</td>
          <td>其他业务收入（元）</td>
          <td style="background-color:#d6ebff;"><input name="10_1" tabindex="10" value="0.00"/></td>
          <td>本期其他业务收入科目贷方发生额合计</td>
        </tr>
        <tr>
          <td>Q0052</td>
          <td>购进运费抵扣税额（元）</td>
          <td style="background-color:#d6ebff;"><input name="11_1" tabindex="11" value="0.00"/></td>
          <td>本期全部运费中用于购进业务的部分抵扣的进项税额合计</td>
        </tr>
        <tr>
          <td>Q0053</td>
          <td>销售运费抵扣税额（元）</td>
          <td style="background-color:#d6ebff;"><input name="12_1" tabindex="12" value="0.00"/></td>
          <td>本期全部运费中用于销售业务的部分抵扣的进项税额合计</td>
        </tr>
      </table>
    </form>
    <p style="text-align:right;">（纳税人盖章）</p>
    <div style="margin-top: 15px" class="uk-text-center">
      <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
      <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
      </button>
      <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 28,  //当前表格的名称编号
        setting: null,
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      this.inputChangeEvent()
      this.inputClickEvent()
      this.getSetting()  //获取后台设置数据
    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // axios.get('getSetting').then(res => {})
        //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

  .bg-write {
    background: #d6ebff;
  }

  .bg-unwrite {
    background: #ccccff;
  }

</style>



